body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}
.flex-center{
display: flex;
justify-content:center;
align-items: center;

}
.header{
padding: 20px 100px;
background-color:#0062ff;
color: white;

font-size: 25px;
font-family: 'Courier New', Courier, monospace;
}

.container{
  height:80vh;
  font-size: 25px;
  font-family: 'Courier New', Courier, monospace;
}
.quiz-start{
flex-direction:column;
gap:20px;
}

.quiz-start button{
  background-color: #00ff40;
  color:black;
  padding:7px 14px;
  border-radius: 3px;
}

.wrapper{
  border:1px solid grey;
  border-radius:3px;
  padding:20px;
  min-width:800px;
  display:flex;
  flex-direction: column;
  gap:20px;

}

.options{
  display:flex;
  flex-direction: column;
  gap:20px;

}
.questions{
  font-weight : bold;
}

.option input{
  margin-right:5px;
}

.wrapper button{
  background-color: black;
  color: aliceblue;
  padding: 7px;
  border-radius: 3px;
}
.result{
font-size: 25px;
font-family: 'Courier New', Courier, monospace;
text-align: center;
}

.result.text{
  line-height:2;
}
.result button{
  background-color:green;
  color: black;
  padding: 7px 14px;
  border-radius: 3px;
  margin-top:10px;
}